import React, { useEffect, useState } from "react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Select, SelectTrigger, SelectContent, SelectItem, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";

// (Optional) Type helpers (can move to a types file if desired)
type SessionUser = { user: string; username: string; password: string };
type SessionsByKey = Record<string, SessionUser[]>;

function ConfigurationPage() {
  // --- Simple User Session Config (BEGIN) ---
  const [data, setData] = useState<SessionsByKey>({});
  const [keyVal, setKeyVal] = useState("");
  const [userVal, setUserVal] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [msg, setMsg] = useState("");

  const keys = Object.keys(data);
  const usersForKey = data[keyVal] || [];

  useEffect(() => {
    let mounted = true;
    (async () => {
      setLoading(true);
      setMsg("");
      try {
        const res = await fetch("/usersession");
        if (!res.ok) throw new Error("Failed to load");
        const json = (await res.json()) as SessionsByKey;
        if (!mounted) return;
        setData(json);
        const firstKey = Object.keys(json)[0] || "";
        const firstUser = firstKey && json[firstKey][0]?.user ? json[firstKey][0].user : "";
        setKeyVal(firstKey);
        setUserVal(firstUser);
        if (firstKey && firstUser) {
          const u = json[firstKey].find(x => x.user === firstUser);
          setUsername(u?.username || "");
          setPassword(u?.password || "");
        }
      } catch {
        if (mounted) setMsg("Could not load sessions.");
      } finally {
        if (mounted) setLoading(false);
      }
    })();
    return () => { mounted = false; };
  }, []);

  useEffect(() => {
    // When key changes, pick first user and fill fields
    const firstUser = usersForKey[0]?.user || "";
    setUserVal(firstUser);
    if (firstUser) {
      setUsername(usersForKey[0]?.username || "");
      setPassword(usersForKey[0]?.password || "");
    } else {
      setUsername("");
      setPassword("");
    }
  }, [keyVal, usersForKey]);

  useEffect(() => {
    // When user changes, fill fields
    if (userVal) {
      const u = usersForKey.find(x => x.user === userVal);
      setUsername(u?.username || "");
      setPassword(u?.password || "");
    } else {
      setUsername("");
      setPassword("");
    }
  }, [userVal, usersForKey]);

  async function handleSave() {
    setSaving(true);
    setMsg("");
    try {
      const res = await fetch("/usersession", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ key: keyVal, user: userVal, username, password })
      });
      setMsg(res.ok ? "Saved." : "Save failed.");
    } catch {
      setMsg("Save failed.");
    } finally {
      setSaving(false);
    }
  }

  const disabled = loading || saving || keys.length === 0;
  // --- Simple User Session Config (END) ---

  return (
    <>
      {/* --- User Session Configuration UI (BEGIN) --- */}
      <Card className="max-w-xl mt-6">
        <CardHeader>
          <CardTitle>User Session Configuration</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Row 1: Key + User */}
          <div className="grid grid-cols-2 gap-4">
            <div className="flex flex-col space-y-2">
              <Label>Key</Label>
              <Select value={keyVal} onValueChange={setKeyVal} disabled={disabled || keys.length === 0}>
                <SelectTrigger>
                  <SelectValue placeholder={loading ? "Loading..." : "Select key"} />
                </SelectTrigger>
                <SelectContent>
                  {keys.map(k => (
                    <SelectItem key={k} value={k}>{k}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="flex flex-col space-y-2">
              <Label>User</Label>
              <Select value={userVal} onValueChange={setUserVal} disabled={disabled || usersForKey.length === 0}>
                <SelectTrigger>
                  <SelectValue placeholder={loading ? "Loading..." : "Select user"} />
                </SelectTrigger>
                <SelectContent>
                  {usersForKey.map(u => (
                    <SelectItem key={u.user} value={u.user}>{u.user}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Row 2: Username */}
          <div className="flex flex-col space-y-2">
            <Label>Username</Label>
            <Input
              value={username}
              onChange={e => setUsername(e.target.value)}
              disabled={disabled || !userVal}
              placeholder="Enter username"
            />
          </div>

          {/* Row 3: Password */}
          <div className="flex flex-col space-y-2">
            <Label>Password</Label>
            <Input
              type="password"
              value={password}
              onChange={e => setPassword(e.target.value)}
              disabled={disabled || !userVal}
              placeholder="Enter password"
            />
          </div>

          {/* Row 4: Save */}
          <div className="flex flex-col space-y-2 pt-2">
            <Button onClick={handleSave} disabled={disabled || !keyVal || !userVal}>
              {saving ? "Saving..." : "Save"}
            </Button>
            {msg && <p className="text-sm text-muted-foreground">{msg}</p>}
          </div>
        </CardContent>
      </Card>
      {/* --- User Session Configuration UI (END) --- */}
    </>
  );
}

export default ConfigurationPage;
